nav{
  width: 100%;
  background-color: #A3826C;
  color: #fff;
  padding: 4px 0;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 10;
}

.logo{
  width: 180px;
}

nav ul li{
  list-style: none;
  display: inline-block;
  margin: 5px 20px;
  font-size: 16px;
}